.form{margin:auto;width:33.33%;padding:12px;font-family:system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;color:#7a7a7a}.form .title{font-weight:600;margin-bottom:2.4rem}.form form{border-radius:5px;padding:20px;background-color:#363636;display:flex;flex-direction:column}.form form .field+.field{margin-top:12px}.form form input[type=email],.form form input[type=password],.form form input[type=text]{width:100%;color:#363636;font-size:2.4rem;padding:12px 17px;border-radius:5px;border:unset;border-color:#dbdbdb;outline:none;line-height:1.5;vertical-align:top}.form form input[type=email]:focus,.form form input[type=password]:focus,.form form input[type=text]:focus{border-color:#428bca;box-shadow:0 0 0 .125em rgba(66,139,202,.25)}.form form .tick{display:flex;width:fit-content}.form form .tick:hover{cursor:pointer;color:#ffe08a}.form form .tick span{margin-left:6px}.form form .tick input{cursor:pointer}.form form .field .icon{width:24px;height:25px;margin-right:14px;filter:invert(94%) sepia(75%) saturate(4%) hue-rotate(187deg) brightness(106%) contrast(100%)}.form form .field button{display:flex;align-items:center;justify-content:center}.form form .field .tick-desc{font-size:1.2rem;display:block;margin-top:8px}.form form .or{text-transform:uppercase;font-size:1.2rem;width:100%;height:1px;background-color:#fff;position:relative;margin:32px 0 20px 0 !important}.form form .or::before{content:"Hoặc";position:absolute;padding:8px 12px;background-color:#fff;top:0;left:50%;transform:translate(-50%, -50%)}.form .feature{display:flex;justify-content:flex-end;font-size:1.6rem;margin-top:24px}.form .feature .dot{margin:0 8px}